import { verifyPhone } from '../../../utils/utils.js'
import UserModel from '@/models/user.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
const userModel = new UserModel() 
import { config } from '../../../config.js'
export default {
	data() {
		return {
			surplusSecond: 0, // 还剩多少秒可以重发
			readySendCode: true,  // 是否准备好发送验证码
			verifyCodeTimer: null, //定时器对象
			focusArray: ['name', 'age', 'gender', 'phone', 'verify'],
			focusIndex: null,  //焦点位置
			agreement:false, // 是否同意用户协议
			uploadedAvatar: '',
			avatarChanged: false,
			name: '',
			age: '',
			gender: 0,
			phone: '',
			verifyCode: '',
			genderArray:['男', '女'],
			avatar: '../../../static/icon/add-avatar.png',
			userAgreement: ''
		};
	},
	beforeDestroy() {
		clearInterval(this.verifyCodeTimer)
		this.verifyCodeTimer = null
	},
	onLoad(option) {
		if(option.phone){
			this.phone = option.phone
			this.userAgreement = config.userAgreement
		}
	},
	components:{
		uniPopup
	},
	computed:{
		genderText(){
			return this.genderArray[this.gender]
		},
		// avatar(){
		// 	if(this.avatarChanged){
		// 		return this.uploadedAvatar
		// 	}else{
		// 		if(this.gender == 0){
		// 			return '../../../static/img/avatar-man.png'
		// 		}else{
		// 			return '../../static/img/avatar-women.png'
		// 		}
		// 	}
		// }
	},
	methods:{
		agreementChange(){
			this.agreement = !this.agreement
			
		},
		handleBlur(index){
			let focusArray = this.focusArray
			// if(index != focusArray.length - 1 ){
			// 	if(!this[focusArray[index + 1]]){
			// 		this.focusIndex = index + 1
			// 		return
			// 	}
			// 	this.focusIndex = null
			// 	return
			// }
			// this.focusIndex = null	
		},
		chooseImage(){
			const that = this
			uni.chooseImage({
				count:1,
				sizeType: 'compressed',
				sourceType: ['album', 'camera'],
				success: (res) => {
					that.avatar = res.tempFilePaths[0]
					that.getImageWebUrl(res.tempFilePaths[0],'avatar')
				}
			})
		},
		getImageWebUrl(path,name){
			const that = this
			uni.showLoading({
				title: '上传中，请稍候...'
			})
			uni.uploadFile({
				// 这里修改上传地址
				url: config.base_url + '/user/uploadAvatar',
				filePath: path,
				name: 'file',
				success(res){
					uni.hideLoading()
					let response = res.data
					if(typeof response === 'string'){
						response = JSON.parse(response)
					}
					const { code, message, data } = response
					if(code === '0'){
						uni.showToast({
							title: '上传成功'
						})
						that.avatarChanged = true
						that.uploadedAvatar = data.url
						that.avatar = data.url
						console.log(that)
					}else{
						uni.showToast({
							icon:'none',
							title: message
						})
					}
				},
				fail: res=>{
					uni.showToast({
						icon:'none',
						title: '上传失败，请稍候再试'
					})
				}
			})
		},
		sendVerifyCode(){
			if(!this.readySendCode){
				return 
			}
			if(!this.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				// this.focusIndex = 3
				return
			}
			
			const correctPhone = verifyPhone(this.phone)
			if(!correctPhone){
				uni.showToast({
					icon:'none',
					title:'手机号码错误，请确认'
				})
				// this.focusIndex = 3
				return
			} 
			uni.showLoading({
				mask: true
			})
			userModel.getVerifyCode(this.phone, 'A').then(res=>{
				// 请求成功,并判断code是否正确
				uni.hideLoading()
				const { code, message, data } = res.data 
				if(code === '0'){
					uni.showToast({
						title: '已发送'
					})
					this.surplusSecond = 60
					// this.focusIndex = 4
					this.verifyCodeTimer = setInterval(()=>{
						if(this.surplusSecond > 0){
							this.surplusSecond -= 1
							this.readySendCode = false
						}else{
							this.surplusSecond = 0
							this.readySendCode = true
							clearInterval(this.verifyCodeTimer)
						}
					}, 1000)
					// this.focusIndex = 4	
				}else{
					uni.showToast({
						icon:'none',
						title: message
					})
				}
				
			}).catch(err=>{
				uni.showToast({
					icon:'none',
					title:'获取验证码失败'
				})
			})
		},
		submit(){
			if(!this.name){
				uni.showToast({
					icon:'none',
					title:'请输入姓名'
				})
				this.$nextTick(()=>{
					// this.focusIndex = 0
				})
				return
			}
			if(!this.age){
				uni.showToast({
					icon:'none',
					title:'请输入年龄'
				})
				this.$nextTick(()=>{
					// this.focusIndex = 1
				})
				return
			}
			if(!this.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				this.$nextTick(()=>{
					// this.focusIndex = 3
				})
				return
			}
			if(!this.verifyCode){
				uni.showToast({
					icon:'none',
					title:'请输入验证码'
				})
				this.$nextTick(()=>{
					// this.focusIndex = 4
				})
				return
			}
			if(!this.agreement){
				uni.showToast({
					icon:'none',
					title:'必须同意用户协议'
				})
				return
			}
			uni.showLoading({
				mask: true
			})
			// 再去验证验证码是否正确
			userModel.verifyCode(this.phone, this.verifyCode).then(res=>{
				const {data, message, code} = res.data
				// 执行真正的登录
				if(code == '0'){
					this.realRegister()
				}else{
					uni.hideLoading()
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '注册失败'
				})
			})
		},
		realRegister(){
			const avatar = this.avatarChanged ? this.avatar : ''
			userModel.register(this.name, this.age, this.phone,this.gender, avatar).then(res=>{
				// 注册成功
				const {data, message, code} = res.data
				if(code === '0'){
					uni.hideLoading()
					this.$refs.registerSuccess.open()
					uni.setStorageSync('isLogin',data.isLogin)
					uni.setStorageSync('isRegister',data.isRegister)
					setTimeout(()=>{
						uni.reLaunch({
							url: '../../customer/main/main'
						})
					},2000)	
				}else{
					uni.hideLoading()
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '注册失败'
				})
			})
		},
		toLogin(){
			uni.navigateTo({
				url: '../login/login'
			})
		},
		radioChange(e){
			this.gender = e.detail.value
		},
		showUserAgreement(){
			this.$refs.userAgreement.open()
		},
		closeDialog(){
			this.$refs.userAgreement.close()
		}
	}
}
